<template>
  <div class="ebook-slide-bookmark">
    <div class="slide-bookmark-title">{{$t('book.bookmark')}} {{bookmark ? bookmark.length : 0}}</div>
    <scroll class="slide-bookmark-list">
      <div class="slide-bookmark-item" v-for="(item, index) in bookmark" :key="index" @click="displayBookmark(item.cfi)">
        <div class="slide-bookmark-item-icon">
          <div class="icon-1"></div>
        </div>
        <div class="slide-bookmark-item-text">{{item.text}}</div>
      </div>
    </scroll>
  </div>
</template>

<script>
  import Scroll from '../../../common/Scroll.vue'
  import { ebookMixin } from '../../../../utils/mixin.js'
  import { getBookmark } from '../../../../utils/localStorage.js'
  export default {
    mixins: [ebookMixin],
    components: {
      Scroll
    },
    data() {
      return {
        bookmark: null
      }
    },
    methods: {
      displayBookmark(cfi) {
        this.display(cfi, () => {
          this.showOrHideMenu()
        })
      }
    },
    mounted() {
      this.bookmark = getBookmark(this.fileName)
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss" scoped="scoped">
  @import "../../../../assets/styles/global";
  .ebook-slide-bookmark {
    width: 100%;
    .slide-bookmark-title{
      width: 100%;
      height: px2rem(48);
      font-size: px2rem(14);
      padding: 0 px2rem(15);
      font-weight: bold;
      @include left;
      box-sizing: border-box;
    }
    .slide-bookmark-list{
      padding: 0 px2rem(15);
      box-sizing: border-box;
      .slide-bookmark-item {
        display: flex;
        padding: px2rem(15) 0;
        box-sizing: border-box;
        .slide-bookmark-item-icon{
          flex: 0 0 px2rem(29);
          @include left;
          .icon-1 {
            width: px2rem(20);
            height: px2rem(20);
            font-size: px2rem(12);
            border-radius: 50%;
            background: #bbb;
            @include center;
          }
        }
        .slide-bookmark-item-text{
          font-size: px2rem(14);
          line-height: px2rem(15);
          max-height: px2rem(45);
          @include ellipsis2(3);
        }
      }
    }
  }
</style>
